*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

body {
    background: lightgray;
    color: navy;
    text-align: center;
}

h1 {
    color: white;
}

h3 {
    font-size: 2rem;
    max-width: 50rem;
    margin: 2rem auto;
}

.btn-style {
    display: block;
    padding: .5rem 1rem;
    font-size: 1.5rem;
    margin: 1rem auto;
    border-radius: 2rem;
    border: 1px solid;
    outline: none;
}

.btn-new {
    display: block;
    padding: .5rem 1rem;
    font-size: 1.5rem;
    background-color: linen;
    font-weight: 700;
    border-radius: 2rem;
    border: none;
    margin: 2rem auto;
}

.correct {
    color: green;
    font-weight: 800;
}

.incorrect {
    color: red;
    font-weight: 800;
}

p {
    margin-top: 3rem;
    font-size: 2rem;
}